/**
   * test
   * @param  {Array}  list     
   * @param  {String} keyWord  
   * @return {Array}           
   */
  export const fuzzyQuery = function(list, keyWord) {
  	var reg = new RegExp(keyWord);
  	var arr = [];
  	for (var i = 0; i < list.length; i++) {
  		if (reg.test(list[i].title)) {
  			arr.push(list[i]);
  		}
  	}
  	return arr;
  }
